/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}
html {
	font-size: 15px;
}
.fbs-pricing-table header {
	color: hsl(233, 13%, 49%);
	margin: 3.3rem 0;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}
.fbs-pricing-table .toggle {
	margin-top: 2rem;
	color: hsl(234, 14%, 74%);
	display: flex;
	align-items: center;
}
.fbs-pricing-table .toggle-btn {
	margin: 0 1rem;
}
.fbs-pricing-table .checkbox {
	display: none;
}

.fbs-pricing-table .sub {
	background: linear-gradient(
		135deg,
		rgba(163, 168, 240, 1) 0%,
		rgba(105, 111, 221, 1) 100%
	);
	display: flex;
	justify-content: flex-start;
	align-items: center;
	/* height: 25px;
	width: 50px; */
	height: 1.6rem;
	width: 3.3rem;
	border-radius: 1.6rem;
	padding: 0.3rem;
}
.fbs-pricing-table .circle {
	background-color: #fff;
	height: 1.4rem;
	width: 1.4rem;
	border-radius: 50%;
}
.fbs-pricing-table.fbs-pricing-table
	.checkbox:checked
	+ .sub {
	justify-content: flex-end;
}

.fbs-pricing-table .fbs-cards {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
}

.fbs-pricing-table .fbs-card {
    background: #fff;
    color: hsl(233, 13%, 49%);
    border-radius: 0.8rem;
    padding: 40px 45px;
}

.fbs-pricing-table .fbs-cards .fbs-card.active {
	background: linear-gradient(
		135deg,
		rgba(163, 168, 240, 1) 0%,
		rgba(105, 111, 221, 1) 100%
	);
	color: #fff;
	// display: flex;
	align-items: center;
	transform: scale(1.1);
	z-index: 1;
}
.fbs-pricing-table ul {
	margin: 2.6rem;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-around;
}
.fbs-pricing-table ul li {
	list-style-type: none;
	display: flex;
	justify-content: center;
	width: 100%;
	padding: 1rem 0;
	border-bottom: 2px solid hsla(240, 8%, 85%, 0.582);
}
.fbs-pricing-table h2.price {
    font-size: 3rem;
    color: hsl(232, 13%, 33%);
    padding-bottom: 2rem;
    font-weight: 600;
    text-align: center;
}
.fbs-pricing-table .shadow {
	box-shadow: -5px 5px 15px 1px rgba(0, 0, 0, 0.1);
}

.fbs-pricing-table .fbs-card.active .price {
	color: #fff;
}

.fbs-pricing-table a .btn {
	margin-top: 1rem;
	height: 2.6rem;
	width: 13.3rem;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 4px;
	background: linear-gradient(
		135deg,
		rgba(163, 168, 240, 1) 0%,
		rgba(105, 111, 221, 1) 100%
	);
	color: #fff;
	outline: none;
	border: 0;
	font-weight: bold;

}
.fbs-pricing-table .fbs-cards .fbs-card a {
	text-decoration: none;
}
.fbs-pricing-table .fbs-card.active button.btn.active-btn {
	background: #fff;
	color: hsl(237, 63%, 64%);
}
.fbs-pricing-table .bottom-bar {
	border-bottom: 2px solid hsla(240, 8%, 85%, 0.582);
}
.fbs-pricing-table .fbs-card.active .bottom-bar {
	border-bottom: 2px solid hsla(240, 8%, 85%, 0.253);
}
.fbs-pricing-table .pack {
    font-size: 1.1rem;
    text-align: center;
    padding: 1rem 0;
}



.fbs-card.active h3.pack {
    color: #fff;
}

@media (max-width: 280px) {
	.fbs-pricing-table ul {
		margin: 1rem;
	}
	.fbs-pricing-table h1 {
		font-size: 1.2rem;
	}
	.fbs-pricing-table .toggle {
		display: flex;
		flex-direction: column;
		justify-content: space-around;
		align-items: center;
		height: 80px;
	}
	.fbs-pricing-table .fbs-cards {
		margin: 0;
		display: flex;
		flex-direction: column;
	}

	.fbs-pricing-table .fbs-card {
		transform: scale(0.8);
		margin-bottom: 1rem;
	}
	.fbs-pricing-table .fbs-cards .fbs-card.active {
		transform: scale(0.8);
	}
}

@media (min-width: 280px) and (max-width: 320px) {
	.fbs-pricing-table ul {
		margin: 20px;
	}
	.fbs-pricing-table .fbs-cards {
		display: flex;
		flex-direction: column;
	}
	.fbs-pricing-table .fbs-card {
		margin-bottom: 1rem;
	}
	.fbs-pricing-table .fbs-cards .fbs-card.active {
		transform: scale(1);
	}
}

@media (min-width: 320px) and (max-width: 414px) {
	.fbs-pricing-table .fbs-cards {
		display: flex;
		flex-direction: column;
	}
	.fbs-pricing-table .fbs-card {
		margin-bottom: 1rem;
	}
	.fbs-pricing-table .fbs-cards .fbs-card.active {
		transform: scale(1);
	}
}
@media (min-width: 414px) and (max-width: 768px) {
	.fbs-pricing-table .fbs-card {
		margin-bottom: 1rem;
		margin-right: 1rem;
	}
	.fbs-pricing-table .fbs-cards .fbs-card.active {
		transform: scale(1);
	}
}
@media (min-width: 768px) and (max-width: 1046px) {
	.fbs-pricing-table .fbs-cards {
		display: flex;
	}
	.fbs-pricing-table .fbs-card {
		margin-bottom: 1rem;
		margin-right: 1rem;
	}
	.fbs-pricing-table .fbs-cards .fbs-card.active {
		transform: scale(1);
	}
}
